
body {
  margin: 0;
  background-color: #000 !important;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.login-page {
  background-color: #415989 !important;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.ui.inverted.blue-inverted.menu {
  background-image: linear-gradient(135deg, rgb(24,42,115), 0%, rgb(33,138,174), 69%,rgb(32,167,172)) !important;
}




.ui.vertical.menu.sidebar-menu {
  margin-top: 60px !important;
  max-height: calc(100% - 60px) !important;
}

.ui.header .sub.header {
  word-wrap: break-word;
  min-width: 120px;
  
}
.main-content {
  margin-top: 65px;
}

.card-num-board {
  display: flex;

}

.card-num-board .num-text {
  
}

@media (min-width: 768px) {
  .ui.vertical.menu.sidebar-menu {
    visibility: visible;
    transform: translate3d(0,0,0);
  }

  .main-content {
    margin-left: 9rem;
    padding: 5px;
  }
}